<template>
  <div class="Container">
    <h1> Users </h1>
    <div class="create-user">
      <label for="create-user">New User:</label>
      <input type="text" id="create-name" v-model="name" placeholder="name"/>
      <input type="text" id="create-surname" v-model="surname" placeholder="surname"/>
      <input type="text" id="create-user" v-model="user" placeholder="user"/>
      <input type="text" id="create-password" v-model="password" placeholder="password"/>
      <input type="text" id="create-roll" v-model="roll" placeholder="roll"/>
      <input type="text" id="create-text" v-model="text" placeholder="text"/>
      <button v-on:click="createUser">Add</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{ error}} </p>
    <div class="Users-container">
      <div class="user"
        v-for="(user, index) in users"
        v-bind:item="user"
        v-bind:index="index"
        v-bind:key="user._id"
        v-on:dblclick="deleteUser(user._id)"
      >
      {{ `${user.createdAt.getDate()}/${user.createdAt.getMonth()}/${user.createdAt.getFullYear()}`}}
      <p class="text">{{user.name}} {{user.surname}} {{user.user}} {{user.roll}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import UserService from '../UserService';

export default {
  name: 'UserComponent',
  data () {
    return {
      users: [],
      error: '',
      text: '' 
    }
  },

  async created() {
    try {
      this.users = await UserService.getUsers()
    } catch (err) {
      this.error = err.error;
    }
  },
  methods: {
    async createUser() {
      await UserService.insertUser(this.name, this.surname, this.user, this.password, this.roll, this.text);
      this.users = await UserService.getUsers();
    },
    async deleteUser(id) {
      await UserService.deleteUser(id);
      this.usres = await UserService.getUsers();
    }
  }

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.container {
  max-width: 800px;
  margin: 0 auto;
}

p.error {
  border: 1px solid #ff5b5f;
  background-color: #ffc5c1;
  padding: 10px;
  margin-bottom: 15px;
}

div.user {
  position: relative;
  border: 1px solid #5db658;
  background-color: #bcffb8;
  padding: 10x 10x 30x 10x;
  margin-bottom: 15px;
}

div.created-at {
  position: absolute;
  top: 0;
  left: 0;
  padding: 5px 15px 5px 15px;
  background-color: darkgreen;
  color: white;
  font-size: 13px; 
}

p.text {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 0;
}

</style>
