import axios from 'axios';

const url = 'api/users/';

class UserService {

    // Get Users 
    static getUsers() {
        return new Promise(async (resolve, reject) => { 
            try {
                const res = await axios.get(url);
                const ret = res.data;
                resolve (
                    ret.map( user => ({
                        ...user,
                        createdAt: new Date(user.createAt)
                    }))
                )
            } catch (err) {
                reject(err);
            }
        })
    }

    // Create Posts
    static insertUser(name, surname, user, password, roll, text) {
        return axios.post(url, {
            name: name, 
            surname: surname, 
            user: user, 
            passwrod: password, 
            roll: roll, 
            text: text
        })
    }

    // Delete Posts
    static deleteUser(id) {
        return axios.delete(`${url}${id}`);
    }
}

export default UserService;